@import url(https://fonts.googleapis.com/css2?family=Kosugi+Maru&family=Overpass:wght@100;300;400;600;700;800;900&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.ant-layout{font-family:'Overpass', sans-serif}.ant-card{border-radius:12px !important}.ant-modal{font-family:'Overpass', sans-serif}.ant-typography.group-name{color:rgba(0,0,0,0.85);font-size:25px}.ant-typography.group-name:hover{color:rgba(14,14,14,0.85);font-size:25px}.ant-btn.btn-pink{font-weight:700;background-color:#ef92b0;border-color:#ef92b0;color:#fff}.ant-btn.btn-pink:hover,.ant-btn.btn-pink:focus,.ant-btn.btn-pink:active{font-weight:700;background-color:#e76798;border-color:#e76798;color:#fff}.ant-btn.btn-purple{font-weight:700;background-color:#7e0f85;border-color:#7e0f85;color:#fff}.ant-btn.btn-purple:hover,.ant-btn.btn-purple:focus,.ant-btn.btn-purple:active{font-weight:700;background-color:#9f16a8;border-color:#9f16a8;color:#fff}.divider-pink{margin:0px !important;margin:8px 0px !important;border-top:1px solid #ef92b0 !important}.divider-purple{margin:0px !important;margin:8px 0px !important;border-top:1px solid #7e0f85 !important}.modal-value{font-size:16px}

